import Vue from 'vue'
// vuetouter
import VueRouter from 'vue-router'
// 
Vue.use(VueRouter)

//
import Tabbar from '../views/Tabbar'
//
import List from '../views/List'
// 
import Sin from '../views/Sin'

//
import Home from '../views/Tabbar/Home'
//
import Car from '../views/Tabbar/Car'
//
import Fenlei from '../views/Tabbar/Fenlei'
//
import Geren from '../views/Tabbar/Geren'
//
import Photoinfo from '../views/Tabbar/Photoinfo'
//
import Xiangqin from '../views/Tabbar/Xiangqin'

const routes = [{
    name: 'tabbar',
    path: '/tabbar',
    component: Tabbar,
    // 
    children: [{
        name: 'home',
        // path/
        path: 'home',
        component: Home,
    }, {
        name: 'fenlei',
        path: 'fenlei',
        component: Fenlei,
    }, {
        name: 'geren',
        path: 'geren',
        component: Geren,
    }, {
        name: 'car',
        path: 'car',
        component: Car,
    }],
}, {
    name: 'list',
    path: '/list',
    component: List
}, {
    name: 'photoinfo',
    path: '/photoinfo/:id',
    component: Photoinfo
}, {
    name: 'xiangqin',
    path: '/xiangqin',
    component: Xiangqin
}, {
    name: 'sin',
    path: '/sin',
    component: Sin
}, {
    path: '/',
    redirect: () => {
        return '/tabbar/home'
    }
}]

const router = new VueRouter({
    routes //() routes : routes
})


export default router